
<style lang="less" scoped>
 @itemHeight: 90px;
  // 弹出更改性别Modal
  .gender_container {
    max-width: 1040px;
    margin: 0 auto;
    background-color: rgba(0, 0, 0, .6);
    height: 100vh;
    position: absolute;
    bottom: 0;
    width: 100vw;
    z-index: 100;
    &>div {
      position: absolute;
      bottom: .625rem;
      left: 0;
      right: 0;
      &>.change_gender {
        width: 100%;
        &>div {
          margin: 0 auto;
          height: @itemHeight;
          width: 90%;
          background-color: rgba(255, 255, 255, .9);
          text-align: center;
          line-height: @itemHeight;
          color: #055dff;
          font-size: 40px;
        }
        &>div:first-child {
          border-radius: 20px 20px 0 0;
        }
        &>div:last-child {
          border-radius: 0 0 20px 20px;
        }
      }
    }
  }
  .cancel {
    width: 90%;
    margin: 20px auto 0 auto;
    &>div {
      height: @itemHeight;
      background-color: rgba(255, 255, 255, .9);
      text-align: center;
      line-height: @itemHeight;
      color: #055dff;
      font-size: 40px;
      border-radius: 20px;
    }
  }
  .base_info_container {
    background-color: #eee;
  } // 头部
  // 上传 头像部分
  .middel_container {
    background-color: #eee;
    div {
      width: 170px;
      height: 170px;
      border-radius: 50%;
      margin: 0 auto;
      position: relative;
      overflow: hidden;
      margin: 20px auto;
      background-position: center;
      .upload_file {
        position: absolute;
        top: 0;
        left: 0;
        width: 170px;
        height: 170px;
        opacity: 0;
        z-index: 3;
      }
      .upload_cam {
        position: absolute;
        bottom: -20px;
        left: 50%;
        z-index: 2;
        transform: translateX(-50%) scale(0.7);
      }
    }
  } // 上传头像部分
  // 个人信息列表
  .info_list {
    &>div {
      margin-top: 10px;
      &>div {
        display: flex;
        height: @itemHeight;
        background: #fff;
        span {
          display: inline-block;
          flex: 3;
          text-indent: 1em;
          vertical-align: middle;
          height: 70px;
          line-height: 70px;
          color: #777;
          font-size: 25px;
          img {
            vertical-align: middle;
            width: 20px;
            margin-right: 20px;
          }
        }
        span:nth-child(2) {
          text-align: right;
          flex: 7;
          font-size: 25px;
          .userInfo {
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            padding-right: 20px;
          }
        }
      }
    }
  } // 个人信息列表
  // avatar 修改
  .avatar {
    width: 100%;
  }
  .header {
    height: 100px;
    background-color: #fff;
    position: sticky;
    top: 0;
    z-index: 1;
    border-bottom: .0625rem solid rgba(0,0,0,.1);
    div {
      padding-left: 20px;
      height: 100px;
      vertical-align: middle;
      img {
        width: 30px;
        vertical-align: middle;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
      }
    }
    span {
      vertical-align: middle;
      display: inline-block;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      font-size: 30px;
    }
  } // 头部
  .user-insigntrue > span:nth-child(2){
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
</style>

<template>
  <div class="base_info_container">
    <mt-header :title="centerTitle">
      <router-link to="/mine" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>
    <div>
      <div class="middel_container">
        <div>
          <img class="avatar" :src="USER_INFO_OBJECT && USER_INFO_OBJECT.avatar ? USER_INFO_OBJECT.avatar : ''" alt="头像">
          <!-- <div class="avatar" :style="{backgroundImage: 'url('+ USER_INFO_OBJECT && USER_INFO_OBJECT.avatar ? USER_INFO_OBJECT.avatar : '' +')'}"></div> -->
          <img src="../../assets/img/je3sxaa9.png" alt="上传ICO" class="upload_cam">
          <input type="file" name="avater" class="upload_file" accept="image/*" onfocus="this.blur()" v-on:change="changeImg($event)">
        </div>
      </div>
      <div class="info_list">
        <div>
          <div><span>用户ID</span><span class="userid"><span class="userInfo">{{USER_INFO_OBJECT && USER_INFO_OBJECT.userId ? USER_INFO_OBJECT.userId : ''}}</span></span>
          </div>
          <div><span>手机号</span><span class="phone"><span class="userInfo">{{USER_INFO_OBJECT && USER_INFO_OBJECT.mobile ? USER_INFO_OBJECT.mobile : ''}}</span></span>
          </div>
          <div v-if="USER_INFO_OBJECT && USER_INFO_OBJECT.clueCategory === '0'"><span>用户名</span><span class="userName"><span class="userInfo"></span></span>
          </div>
          <div v-if="USER_INFO_OBJECT && USER_INFO_OBJECT.clueCategory === '1'"><span>用户名</span><span class="userName"><span class="userInfo">{{USER_INFO_OBJECT && USER_INFO_OBJECT.qqAccount ? USER_INFO_OBJECT.qqAccount : ''}}</span></span>
          </div>
          <div v-if="USER_INFO_OBJECT && USER_INFO_OBJECT.clueCategory === '2'"><span>用户名</span><span class="userName"><span class="userInfo">{{USER_INFO_OBJECT && USER_INFO_OBJECT.weixinAccount ? USER_INFO_OBJECT.weixinAccount : ''}}</span></span>
          </div>
          <div @click="change_nickName(USER_INFO_OBJECT)"><span>昵称</span><span><span class="userInfo">{{USER_INFO_OBJECT && USER_INFO_OBJECT.nickname ? USER_INFO_OBJECT.nickname : ''}}</span><img :src="direction"></span>
          </div>
          <div><span>班级编号</span><span class="phone"><span class="userInfo">{{USER_INFO_OBJECT && USER_INFO_OBJECT.className ? USER_INFO_OBJECT.className : ''}}</span></span>
          </div>
        </div>
        <div>
          <div @click="changeTrueName(USER_INFO_OBJECT)"><span>真实姓名</span><span><span class="userInfo">{{USER_INFO_OBJECT && USER_INFO_OBJECT.trueName ? USER_INFO_OBJECT.trueName : ''}}</span><img :src="direction"></span>
          </div>
          <div @click="changeGender(USER_INFO_OBJECT)"><span>性别</span><span><span class="userInfo">{{USER_INFO_OBJECT && USER_INFO_OBJECT.gender ? USER_INFO_OBJECT.gender : ''}}</span><img :src="direction"></span>
          </div>
          <div @click="change_address(USER_INFO_OBJECT)"><span>所在地区</span><span><span class="userInfo">{{USER_INFO_OBJECT && USER_INFO_OBJECT.address ? USER_INFO_OBJECT.address : ''}}</span><img :src="direction"></span>
          </div>
          <div class="user-insigntrue" @click="changeSignature(USER_INFO_OBJECT)"><span>个性签名</span><span><span class="userInfo">{{USER_INFO_OBJECT && USER_INFO_OBJECT.introduction ? USER_INFO_OBJECT.introduction : ''}}</span><img :src="direction"></span>
          </div>
        </div>
      </div>
    </div>
    <transition enter-active-class="slideInUp" leave-active-class="slideOutDown">
      <div class="gender_container animated" v-if="show_gender">
        <div>
          <div class=" change_gender">
            <div @click="change_gender(gender)" v-for="(gender,index) in gender_arr" :key="index">{{gender}}</div>
          </div>
          <div class="cancel">
            <div @click="cancel_modal">取消</div>
          </div>
        </div>
      </div>
    </transition>
    <success v-if="showSuccessTip" :title="uploadTitle"></success>
  </div>
</template>

<script>
  import headerNavBack from '@/components/headerNavBack'
  import success from '@/components/success'
  import { createNamespacedHelpers } from 'vuex'
  const { mapMutations, mapActions, mapState} = createNamespacedHelpers('userModule')
  export default {
    components: {
      headerNavBack,
      success
    },
    data() {
      return {
        imgFile: {},
        params: {
          url: null
        },
        show_gender: 0,
        gender_arr: ['男','女','保密'],
        gender_history: null,
        direction: require('../../assets/img/je3skwbl.png'),
        centerTitle: '个人信息',
        rightTitle: '',
        showSuccessTip: 0,
        uploadTitle: '上传成功',
        back_img: require('../../assets/img/per_fh.png'),
      }
    },
    methods: {
      ...mapActions([
        'USER_INFO'
      ]),
      // 显示修改性别弹出层
      changeGender(userInfo) {
        this.show_gender = 1
      },
      // 识别性别，发送请求，修改数据
      change_gender(gender){
        this.$Indicator.open({
          text: '修改中...',
          spinnerType: 'fading-circle'
        })
        switch(gender){
          case '男':
            this.gender_history = 'M'
            break;
          case '女':
            this.gender_history = 'F'
            break;
          case '保密':
            this.gender_history = 'S'
            break;
        }
        this.axios.post('/api/changeGender',{
          gender:this.gender_history
        }).then(data=>{
          if(data.data.status === 'success'){
            this.$Indicator.close()

            this.$Toast({
              message: '修改成功',
              position: 'middle',
              duration: 1000
            })
            this.USER_INFO_OBJECT.gender = gender
            this.show_gender= 0
          }
        }).catch(err=>{
          console.log(err);
        });
      },
      // 隐藏修改性别弹出层
      cancel_modal(){
        this.show_gender= 0;
      },
      change_nickName(name) {
        this.$router.push({
          name: 'changeNickname',
          params: {
            id: name.userId,
            name: name.nickname,
            phone: name.mobile
          }
        });
      },
      change_address(name) {
        this.$router.push({
          name: 'changeAddress',
          params: {
            id: name.userId,
            phone: name.mobile,
            address: name.address,
            familyAddr: name.familyAddress
          }
        });
      },
      changeSignature(name) {
        this.$router.push({
          name: 'changeSignature',
          params: {
            id: name.userId,
            phone: name.mobile,
            intro: name.introduction
          }
        });
      },
      changeTrueName(name) {
        this.$router.push({
          name: 'changeTrueName',
          params: {
            id: name.userId,
            phone: name.mobile,
            truename: name.trueName
          }
        });
      },
      changeImg($event) {
        this.$Indicator.open({
          text: '上传中...',
          spinnerType: 'fading-circle'
        })
        var file = $event.target.files[0]
        this.params.filename = file.name
        //base64
        var reader = new FileReader()
        reader.readAsDataURL(file)
        reader.onload = (event) => {
          this.axios.post('/api/upload_file', {
            content: event.target.result
          }).then(data => {
            this.$store.state.userModule.LOGIN_USER_OBJECT.user.avatar = data.data.data[0]
            this.$store.state.userModule.USER_INFO_OBJECT.avatar = data.data.data[0]
            this.params.url = data.data.data[0]
            this.axios.post('/api/avater', this.params).then((res) => {
              if(res.data.result){
                this.$Indicator.close()
                this.$Toast({
                  message: '上传成功',
                  position: 'middle',
                  duration: 1000
                })
              }
            }).catch(err => {
              console.log(err)
            })
          }).catch((err) => {
            console.log(err)
          })
        }
      },
      backEvent(){
        this.$router.push('/mine')
      }
    },
    mounted() {
      let id = null
      if (this.$route.params.id) {
        id = this.$route.params.id
      } else {
        this.$router.push('/mine')
      }
      if(!this.USER_INFO_OBJECT){
        this.$Indicator.open({
          text: '修改中...',
          spinnerType: 'fading-circle'
        })
        this.USER_INFO({
          params: {
            id: id
          }
        }).then(data => {
          this.$Indicator.close()
        }).catch(err => {
          console.log(err)
        })
      }else{
        this.$Indicator.close()
      }
    },
    computed: mapState([
        'USER_INFO_OBJECT',
        'LOGIN_USER_OBJECT'
    ]),
    watch: {
      USER_INFO_OBJECT(state){

      },
      LOGIN_USER_OBJECT(state){

      }
    }
  }
</script>



